.calendar {
  width: 350px;
  margin: 0 auto; }
  .calendar .header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    border-bottom: 1px solid #ccc; }
    .calendar .header h1 {
      font-size: 18px; }
    .calendar .header i {
      font-size: 12px;
      margin-top: 18px; }
  .calendar .week {
    border-bottom: 1px solid #ccc; }
    .calendar .week span {
      padding: 15px;
      width: 5.2%;
      display: inline-block;
      border-left: 1px solid #ccc; }
      .calendar .week span:last-child {
        border-right: 1px solid #ccc; }
      .calendar .week span.disabled {
        color: #ccc; }
      .calendar .week span.selected {
        color: #fff;
        background-color: #2eb1a5; }
  .calendar .info {
    font-size: 14px;
    text-align: center;
    margin-top: 5px; }



